import React from 'react'
import { Progress } from 'rsuite';
import { AiTwotoneStar } from 'react-icons/ai'
import SimilarMovies from '../components/SimilarMovies';
const WatchNow = ({ watchData, similarData, getWatchData }) => {
    const data = watchData;
    const price = data?.id?.toString().slice(0, 3);
    console.log(price)

    return (
        <div className='cursor-default flex flex-col'>
            <div className='flex justify-center text-gray-800 '>
                <p className='text-3xl'>Overview</p>
            </div>
            {data &&
                <div className='bg-orange-200 relative '>
                    <div className="absolute top-10 flex w-full flex-wrap justify-around z-50 ">
                        <div>
                            <img src={`https://image.tmdb.org/t/p/w500/${data.image}`} className=' rounded-xl h-[60vh] w-[18rem] opacity-100' alt="" />
                        </div>
                        <div className='w-[70rem] flex gap-2 flex-col'>
                            <p className='text-4xl font-extrabold  text-black'>{data.title}</p>
                            <p className='text-xl font-bold text-black'>OverView</p>
                            <p className='text-lg'>{data.overview}</p>
                            <p className='text-black text-sm'>Release Date :- {data.date}</p>
                            <p className='text-black text-sm'>Language :- {data.date === 'hi' ? 'Hindi' : 'English'}</p>
                            <div className='flex gap-4'>
                                <p className='text-lg font-bold mt-1 text-black'>popularity</p>
                                <div className='text-white bg-black w-10 rounded-full'>
                                    <Progress.Circle strokeColor='green' percent={data.rating * 10} />
                                </div>
                            </div>
                            <p className='text-lg text-black flex gap-1'>IMDB Rating <span className='flex flex-row'>{data.rating}</span>
                                <span className='mt-1'><AiTwotoneStar /></span>
                            </p>
                            <button className='bg-yellow-600 flex justify-center w-2/12 rounded-sm text-lg text-black px-10 py-1 mt-3'>Buy ₹{price}</button>
                            <div className='flex mt-5 border-2  gap-5 w-3/12 bg-transparent p-4 rounded-lg'>
                                <p className='text-lg text-black items-center flex'>Available On : </p>
                                <div className='flex gap-2 justify-center items-center'>
                                    <a href="https://www.netflix.com/in/" target="_blank" rel="noreferrer">
                                        <img className='h-7  border-solid border-e-2 border-gray-100 ' src="./assets/netflix.png" alt="" />
                                    </a>
                                    <a href="https://www.primevideo.com/offers/nonprimehomepage/ref=dv_web_force_root" target="_blank" rel="noreferrer">
                                        <img className='h-7 pr-2 border-solid border-e-2 border-gray-100 ' src="./assets/social.png" alt="" />
                                    </a>
                                    <a href="https://www.jiocinema.com/tv-shows/" target='_blank rel="noreferrer"'>
                                        <img className='h-7' src="./assets/video-player.png" alt="" />
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            }

            {data &&
                <div className='h-[70vh]'>
                    <img src={`https://image.tmdb.org/t/p/w500/${data.background}`} className='w-screen h-[550px] opacity-30 z-0' alt="" />
                </div>
            }
            <SimilarMovies similarData={similarData} getWatchData={getWatchData} />
        </div>
    )
}

export default WatchNow
